/* AM Collapse */
.am-collapse {
  overflow: visible;
  overflow: initial;
}
.am-collapse.ng-animate {
  overflow: hidden;
}
.am-collapse.am-collapse-add,
.am-collapse.ng-hide-remove,
.am-collapse.ng-move,
.am-collapse.ng-enter,
.am-collapse.in-add {
  -webkit-animation-name: expand;
  animation-name: expand;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-timing-function: ease;
  animation-timing-function: ease;
}
.am-collapse.am-collapse-remove,
.am-collapse.ng-hide,
.am-collapse.ng-leave,
.am-collapse.in-remove {
  max-height: 0;
  display: block;
  -webkit-animation-name: collapse;
  animation-name: collapse;
  -webkit-animation-timing-function: cubic-bezier(0, 0.6, 0, 1);
  animation-timing-function: cubic-bezier(0, 0.6, 0, 1);
}
/* Modal Animation */
.modal.am-fade.ng-enter {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}
.modal.am-fade.ng-enter .modal-content {
  -webkit-transform: translate(0, -50px);
  -ms-transform: translate(0, -50px);
  -o-transform: translate(0, -50px);
  transform: translate(0, -50px);
}
.modal.am-fade.ng-enter.ng-enter-active .modal-content {
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  -o-transform: translate(0, 0);
  transform: translate(0, 0);
  -webkit-transition: transform 0.3s ease;
  -o-transition: transform 0.3s ease;
  transition: transform 0.3s ease;
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
}
.modal.am-fade.ng-leave .modal-content {
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  -o-transform: translate(0, 0);
  transform: translate(0, 0);
}
.modal.am-fade.ng-leave.ng-leave-active .modal-content {
  -webkit-transform: translate(0, -50px);
  -ms-transform: translate(0, -50px);
  -o-transform: translate(0, -50px);
  transform: translate(0, -50px);
  -webkit-transition: transform 0.3s ease;
  -o-transition: transform 0.3s ease;
  transition: transform 0.3s ease;
}
/* BS3 Tooltips */
.tooltip {
  -webkit-animation-duration: 0.1s;
  animation-duration: 0.1s;
}
