/* ANIMATIONS KEYFRAMES ================================================================ */
/* AngularMotion Collapse Override */
@keyframes expand {
  from {
    max-height: 0;
  }
  to {
    max-height: calc(100vh - 60px - 40px);
  }
}
@keyframes collapse {
  from {
    max-height: calc(100vh - 60px - 40px);
  }
  to {
    max-height: 0;
  }
}
@-ms-keyframes expand {
  from {
    max-height: 0;
  }
  to {
    max-height: 750px;
  }
}
@-ms-keyframes collapse {
  from {
    max-height: 750px;
  }
  to {
    max-height: 0;
  }
}
