/* BUTTONS ----------------------------------- */
.btn {
  font-size: 1em;
  -webkit-appearance: none;
  -webkit-transition: all 0.25s ease;
  -o-transition: all 0.25s ease;
  transition: all 0.25s ease;
}
.btn.btn-lg,
.btn-group-lg > .btn {
  font-size: 1.2em;
}
.btn.btn-sm,
.btn-group-sm > .btn {
  font-size: 0.85em;
}
.btn.btn-xs,
.btn-group-xs > .btn {
  font-size: 0.75em;
}
.btn > .fa,
.btn > .glyphicon {
  line-height: 1em;
}
.btn.disabled > .text-primary,
.btn[disabled] > .text-primary,
fieldset[disabled] .btn > .text-primary,
.btn.disabled > .text-warning,
.btn[disabled] > .text-warning,
fieldset[disabled] .btn > .text-warning,
.btn.disabled > .text-danger,
.btn[disabled] > .text-danger,
fieldset[disabled] .btn > .text-danger,
.btn.disabled > .text-info,
.btn[disabled] > .text-info,
fieldset[disabled] .btn > .text-info {
  color: inherit;
}
/* Circle Buttons */
.btn-circle {
  /*@roundSize-base:round((@padding-base-vertical * 2) + (@font-size-base * @line-height-base));
  @roundSize-large:round((@padding-large-vertical * 2) + (@font-size-large * @line-height-large));
  @roundSize-small:round((@padding-small-vertical * 2) + (@font-size-small * @line-height-small));
  @roundSize-xs:round((@padding-xs-vertical * 2) + (@font-size-small * @line-height-small));*/
  padding: 0;
  line-height: 2.515em;
  line-height: calc(2.715em - 2px);
  text-align: center;
  width: 2.715em;
  height: 2.715em;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  border-radius: 100px;
}
.btn-circle.btn-lg {
  line-height: 2.42em;
  line-height: calc(2.62em - 2px);
  text-align: center;
  width: 2.62em;
  height: 2.62em;
}
.btn-circle.btn-sm {
  line-height: 2.1em;
  line-height: calc(2.1em - 2px);
  font-size: 0.95em;
  text-align: center;
  width: 2.1em;
  height: 2.1em;
}
.btn-circle.btn-xs {
  line-height: 1.705em;
  line-height: calc(1.905em - 2px);
  text-align: center;
  width: 1.905em;
  height: 1.905em;
