/* Pace Loader */
.pace .pace-progress {
  height: 3px;
  background: #258FC9;
  -webkit-transition: width 1s;
  -o-transition: width 1s;
  transition: width 1s;
}
.pace-active .pace-progress {
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  -o-transform: translate(0, 0);
  transform: translate(0, 0);
  -webkit-transition: width 1s, transform 0.5s 0s ease-out;
  -o-transition: width 1s, transform 0.5s 0s ease-out;
  transition: width 1s, transform 0.5s 0s ease-out;
}
.pace-inactive {
  display: block;
}
.pace-inactive .pace-progress {
  -webkit-transform: translate(0, -100%);
  -ms-transform: translate(0, -100%);
  -o-transform: translate(0, -100%);
  transform: translate(0, -100%);
  -webkit-transition: width 1s, transform 0.6s 0.75s ease-out;
  -o-transition: width 1s, transform 0.6s 0.75s ease-out;
  transition: width 1s, transform 0.6s 0.75s ease-out;
}
